.footer {
  width: 100%;
  .container2 {
    background-color: rgb(6, 114, 203);
    .content {
      padding: 48px 18px;
      justify-content: center;
      .layout {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-content: space-evenly;
        gap: 4rem;
        .panel_logo {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          a {
            text-decoration: none;
            img {
              width: 240px;
              height: 240px;
            }
          }
          .resena {
            .resena-titulo {
              margin: 0;
              padding: 0;
              line-height: 100%;
              font-size: 1.8rem;
              font-weight: 600;
              text-wrap: nowrap;
              color: lightgray;
            }
            .resena-subtitulo {
              margin: 0;
              padding: 0;
              line-height: 100%;
              font-size: 1.1rem;
              font-weight: 500;
              text-wrap: nowrap;
              color: lightgray;
            }
            .resena-otros {
              margin: 12px;
              padding: 0;
              line-height: 100%;
              font-size: 0.95rem;
              font-weight: 400;
              text-wrap: nowrap;
              color: white;
            }
          }
        }
      }
    }
  }
}

.panel_info {
  display: inline-block;
  color: white;
  ul {
    list-style: none;
    padding: 18px 0px 18px 0px;
    li {
      margin: 6px 0px 6px 0px;
      line-height: 100%;
      font-weight: 400;
      color: white;
      a {
        text-decoration: none;
      }
      a:hover {
        color: darkgrey;
      }
      span {
        font-size: 0.95rem;
      }
    }
  }
}

.panel_info_titulo {
  margin: 12px 0px 12px 0px;
  font-size: 1.6rem;
  font-weight: 600;
}

.panel_info_subtitulo {
  margin: 24px 0px 12px 0px;
  font-size: 1.2rem;
  font-weight: 500;
}

.panel_info_texto {
  margin: 6px 0px 6px 0px;
  font-size: 1.05rem;
  font-weight: 400;
}

