  position: relative;
  margin: 32px 48px 32px 48px;
  list-style-type: none;
  .contenido {
    display: inline-block;
    /* height: 100vh; */
    width: 100wh;
    /* margin: 32px 48px 32px 48px; */
    /* padding: 32px 48px 32px 48px; */
    border: 1px solid grey;
    border-radius: 12px;
    padding: 6px;
    background-color: rgb(255, 255, 255);
    /* gap: 2.5rem 4rem; */
  }
}

.wrapper {
  padding: 32px 48px 32px 48px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}

.layer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  text-align: center;
}

.layer-dos {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  text-align: center;
}

.titulares {
  .tit-ppal {
    margin: 12px;
    .tit-ppal-titulo {
      font-size: 1.8rem;
      font-weight: 600;
      line-height: 90%;
    }
    .tit-ppal-detalle {
      padding: 2px 2px 18px 2px;
      font-size: 1.05rem;
      font-weight: 400;
      line-height: 110%;
      text-align: justify;
    }
    .tit-ppal-imagen {
      margin: 0 auto;
      width: 100%;
      height: auto;
      border-radius: 6px;
    }
    p:hover {
      cursor: pointer;
    }
    img:hover {
      cursor: pointer;
    }
  }
}

.col-dos {
  margin: 12px 6px;
  flex-basis: 42%;
  .col-dos-imagen {
    margin: 0 auto;
    width: 100%;
    height: 160px;
    border-radius: 6px;
  }
  .col-dos-detalle {
    padding: 24px 2px 6px 2px;
    font-size: 1.05rem;
    font-weight: 400;
    line-height: 110%;
    text-align: justify;
  }
  p:hover {
    cursor: pointer;
  }
  img:hover {
    cursor: pointer;
  }
}

.border {
  border: solid 1px rgb(6, 114, 203);
  border-radius: 8px;
}

.flex-row {
  position: relative;
}

.flex-row:after {
  position: absolute;
  z-index: 1;
}

